.port-page {
    background-image: url(../imgs/about-background.jpg);
    background-size: cover;
    background-position: center;
    color: white;
    margin: 0;
    padding: 0;
}

.pro-title {
    text-align: center;
    padding: 3%;
    font-size: 48px;
}


.hide {
    display: none;
    text-align: center;
}

.projects {
    padding: 20px;
    margin-left: 20px;
}

.project {
    border: 5px solid black;
    border-radius: 15px;
    width: 90%;
    height: 300px;
}

.project:hover + .hide {
    display: block;
}